/* hier komt de specifieke styling van een blok */

.block.block-1{
   transform: rotate(0deg);
}

@include tablet-landscape{
    .block.block-1{
       transform: rotate(0deg);
    }
}

@include tablet-portrait{
    .block.block-1{
       transform: rotate(0deg);
    }
}

@include mobile-landscape{
    .block.block-1{
       transform: rotate(0deg);
    }
}

@include mobile-portrait{
    .block.block-1{
       transform: rotate(0deg);
    }
}

.container-news{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex: 0 1 calc(25% - 30px);
  box-sizing: border-box;
  width: 100%;
  height: 580px;
  background-color: #F5F5F5;
  border-radius:0px 30px 0px 30px;
}

.item-01-container{
  width: 288px;
  display: inline-block;
  box-sizing: border-box;
  margin-right: 15px;
}

.item-01-container-img{
  width: 288px;
}

.item-01-container-content{
  order: 1;
  width: 288px;
  font-family: Roboto;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.6;
  letter-spacing: normal;
  color: #333333;
  margin: 10px;
  display: inline-block;
}

